import { ComponentStylingModel, ComponentStylingPropertiesControlComponent, genericPropertiesControlFor } from '@anglr/dynamic/layout-editor';
import { MaterialCheckboxModel } from './checkbox.model';
/**
 * Material checkbox layout metadata
 */

export class MaterialCheckboxLayoutEditorMetadata {
  //######################### constructor #########################
  constructor() {
    //######################### public properties - implementation of LayoutEditorMetadataDescriptor #########################

    /**
     * @inheritdoc
     */
    this.metaInfo = {
      name: 'Checkbox',
      description: 'Material checkbox',
      optionsMetadata: {
        propertiesMetadata: [{
          modelType: ComponentStylingModel,
          propertiesControls: [ComponentStylingPropertiesControlComponent]
        }, {
          modelType: MaterialCheckboxModel,
          propertiesControls: [genericPropertiesControlFor(['label', 'controlName'])]
        }]
      },
      defaultOptions: {
        label: 'Input label'
      },
      group: 'Material form fields'
    };
    Object.freeze(this);
  }

}